import React, { memo, useEffect } from 'react';
import { makeStyles, Grid, Typography, Modal, Fade, Backdrop, Link, Chip, Switch } from '@material-ui/core';
import { MakeDestination } from '../Custom/MakeDesination';
import CloseIcon from '@material-ui/icons/Close';

const SergeInfoModal = memo(({ data, open, handleClose }) => {
  const classes = useStyles();
  const { transformer } = MakeDestination();
  const dict = {
    격식있: '격식있는',
    단순: '단순한',
    모던: '모던한',
    자연: '자연의',
    자유로: '자유로운',
    전통적인: '전통적인',
    차분: '차분한',
    추상적: '추상적인',
    친근: '친근한',
    화려: '화려한',
    기타: '기타',
  };
  const moodTags = data?.Mood
    ? Object.entries(data.Mood[0]).map(([key, value]) => {
        return (
          <>
            <b>{dict[key] || key}</b> {Math.round(Number(value * 100)) / 100}
          </>
        );
      })
    : [];

  const headerItems = [
    { title: '상표유형', val: data?.상표유형코드, type: 'text' },
    { title: '출원번호 / 일자', val: data?.출원번호 && data?.출원일자 ? `${data?.출원번호} / ${data?.출원일자}` : null, type: 'text' },
    { title: '공고번호 / 일자', val: data?.공고번호 && data?.공고일자 ? `${data?.공고번호} / ${data?.공고일자}` : null, type: 'text' },
    { title: '등록번호 / 일자', val: data?.등록번호 && data?.등록일자 ? `${data?.등록번호} / ${data?.등록일자}` : null, type: 'text' },
    {
      title: '최종처분코드(처분일자)',
      val: data?.최종처분코드 && data?.최종처분일자 ? `${data?.최종처분코드}(${data?.최종처분일자})` : null,
      type: 'chip',
      color: 'primary',
    },
    { title: '최종소급일자', val: data?.최종소급일자, type: 'text' },
    { title: '현재상태', val: data?.법적상태, type: 'emphasized' },
    { title: '서지정보 상세보기', val: data?.상세정보URL, type: 'link' },
  ];

  const descriptionItems = [
    { title: '상표명칭(한글)', val: data?.상표한글명, type: 'text' },
    { title: '상표명칭(영문)', val: data?.상표영문명, type: 'text' },
    { title: '지정상품', val: data?.지정상품한글명 || [], type: 'group', variant: 'outlined' },
    {
      type: 'groupItem',
      data: [
        { title: '이미지 유무', val: data?.이미지유무 === 'Y', type: 'switch' },
        { title: '공공공보유무', val: data?.공공공보유무 === 'Y', type: 'switch' },
        { title: '정정공보유무', val: data?.정정공보유무 === 'Y', type: 'switch' },
      ],
    },
    {
      type: 'groupItem',
      data: [
        { title: '원출원유무', val: data?.이미지유무 === 'Y', type: 'switch' },
        { title: '심판청구유무', val: data?.심판청구유무 === 'Y', type: 'switch' },
        { title: '우선권주장유무', val: data?.우선권주장유무 === 'Y', type: 'switch' },
      ],
    },
    {
      title: '마드리드 국제등록번호 / 일자',
      val: data?.마드리드국제등록번호 && data?.마드리드국제등록일자 ? `${data?.마드리드국제등록번호} / ${data?.마드리드국제등록일자}` : null,
      type: 'text',
    },
    { title: '마드리드 사후 등록일자', val: data?.마드리드사후등록일자, type: 'text' },
  ];

  return (
    <Modal
      aria-labelledby='serge-info-modal-header'
      aria-describedby='serge-info-modal-description'
      className={classes.modal}
      open={open}
      onClose={handleClose}
      closeAfterTransition
      BackdropComponent={Backdrop}
      BackdropProps={{
        timeout: 500,
      }}
    >
      <Fade in={open}>
        <Grid container className={classes.paper} direction='column' spacing={4}>
          <Grid item xs={12} style={{ textAlign: 'right', fontWeight: 'bold', cursor: 'pointer' }}>
            <CloseIcon onClick={handleClose} />
          </Grid>
          <Grid container id='serge-info-modal-header' className={classes.header}>
            <Grid item md={4}>
              <Grid container direction='column'>
                <div className='imgContainer'>
                  <img src={transformer(Array.isArray(data?.이미지경로) ? data?.이미지경로[0] : data?.이미지경로 || '')} alt='logo' />
                </div>
                <Item title='Mood 키워드 분석 결과' val={moodTags} type='group' style={{ border: 'none' }} color='secondary' />
              </Grid>
            </Grid>
            <Grid item md={7}>
              {headerItems.map((v, i) => (
                <Item key={i} {...v} />
              ))}
            </Grid>
          </Grid>
          <Grid container xs={12} id='serge-info-modal-description' className={classes.description}>
            {descriptionItems.map((v, i) => {
              if (v.type === 'groupItem') return <GroupItem key={i} items={v.data} />;
              return <Item key={i} {...v} />;
            })}
          </Grid>
        </Grid>
      </Fade>
    </Modal>
  );
});

const useStyles = makeStyles(theme => ({
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
    position: 'absolute',
    left: '50%',
    top: '50%',
    maxWidth: '70%',
    maxHeight: '80%',
    transform: 'translate(-50%, -50%)',
    flexWrap: 'nowrap',
    overflowY: 'auto',
    gap: 16,
  },
  header: {
    alignItems: 'center',
    justifyContent: 'center',
    borderTop: '1px solid rgba(0,0,0,0.3)',
    borderBottom: '1px solid rgba(0,0,0,0.3)',
    padding: '16px 0px',
    gap: 32,
    '& .imgContainer': {
      display: 'flex',
      alignItems: 'center',
      height: 350,
      maxWidth: '100%',
    },
    '& img': {
      width: '100%',
      maxHeight: '350px',
    },
  },
  description: {
    alignItems: 'center',
    justifyContent: 'center',
    gap: 4,
    padding: '16px',
  },
}));

const GroupItem = ({ items }) => {
  return (
    <Grid container wrap='nowrap'>
      {items.map((v, i) => (
        <Item key={i} {...v} />
      ))}
    </Grid>
  );
};

const Item = ({ title, val, type, style, ...options }) => {
  const renderedVal = val => {
    if (type === 'text' || !type) {
      return (
        <Grid item>
          <Typography variant='subtitle1' {...options}>
            {val ?? '없음'}
          </Typography>
        </Grid>
      );
    } else if (type === 'link') {
      return (
        <Grid item>
          <Link href={val ?? '없음'} variant='subtitle1' rel='noreferrer' target='_blank' {...options}>
            바로가기
          </Link>
        </Grid>
      );
    } else if (type === 'emphasized') {
      return (
        <Grid item>
          <Typography variant='h6' color='primary' {...options}>
            {val ?? '없음'}
          </Typography>
        </Grid>
      );
    } else if (type === 'chip') {
      return (
        <Grid item style={style}>
          <Chip size='small' label={val ?? '없음'} color='secondary' {...options} />
        </Grid>
      );
    } else if (type === 'switch') {
      return <Switch checked={val} color='secondary' {...options} />;
    } else if (type === 'group') {
      return (
        <Grid container alignItems='center' style={{ border: '1px solid rgba(0,0,0,0.3)', gap: 8, padding: 8, ...style }}>
          {val.map((v, i) => (
            <Chip size='small' label={v ?? '없음'} color='primary' key={i} {...options} />
          ))}
        </Grid>
      );
    }
  };
  return (
    <Grid container alignItems='center' style={{ gap: 8 }}>
      <Typography variant='h6' component='h6'>
        {title}
      </Typography>
      {renderedVal(val)}
    </Grid>
  );
};

export default SergeInfoModal;
